import { Component, h } from 'preact';
import { Link } from 'preact-router/match';
// @ts-ignore
import style from './style.css';

const ROTATION = 'yr124eFfeqeeeere1re23e124q'; 

class Props{

}

class TypedSequence {
	constructor(options: any){
		return Object.assign(this, options);
	}

	userInput: string = '';
}

class TypedSequenceValidator {
	validate(userInput: TypedSequence, key: string):boolean {
		let value = userInput.userInput + key;
		return ROTATION.startsWith(value);
	}
}

class Sequence extends Component<Props, TypedSequence> {
	state = new TypedSequence({userInput: ''});

	validator = new TypedSequenceValidator();

	constructor() {
	  super();
	}
	
	_handleKeyDown = (event: KeyboardEvent) => {
		if(event.key === 'Shift')
			return;
		if(event.key === 'Control')
			return;
		if(event.key === 'AltGraph')
			return;
		if(event.key === 'Control')
			return;
		if(event.key === 'Alt')
			return;
		if(event.key === 'CapsLock')
			return;

		this.setState((pState:TypedSequence)=>{
			if(this.validator.validate(pState, event.key)){
				return {userInput: pState.userInput + event.key};
			} else {
				return {userInput: ''};
			}			 
		});
		console.log(event);
		event.preventDefault();
		event.stopPropagation();
		return true;
	}
  
	// Lifecycle: Called whenever our component is created
	componentDidMount() {
		document.addEventListener("keydown", this._handleKeyDown);
	}
  
	// Lifecycle: Called just before our component will be destroyed
	componentWillUnmount() {
		document.removeEventListener("keydown", this._handleKeyDown);
	}
  
	render() {
	  return (<div>{this.state.userInput.split('').map(x => <span>ðŸŸ¢</span>)}</div>);
	}
}

export default Sequence;
